$brk-base-colors: ();
$brk-base-colors: map-merge((
				"brand-primary":      darken(adjust-hue(#2775FF, -39), 13.73),
				"secondary":          lighten(darken(adjust-hue(#7202bb, -109), 2.12), 6.08),
				"white":              #fff,
				"black":              #000,
				"brk-dark-base":      #272727,
				"brk-base-1":         darken(adjust-hue(#69adff, -39), 13.73),
				"brk-base-2":         darken(adjust-hue(#00c6ff, -39), 13.73),
				"brk-base-3":         darken(adjust-hue(#0F5AE0, -39), 13.73),
				"brk-base-4":         #e8ecff,
				"brk-base-5":         darken(adjust-hue(#00f6ff, -39), 13.73),
				"brk-base-6":         darken(adjust-hue(#0B98F6, -39), 13.73),
				"brk-base-7":         darken(adjust-hue(#a7ebff, -39), 13.73),
				"brk-base-8":         darken(adjust-hue(#1600ba, -39), 13.73),// TODO: !!!
				"brk-secondary-1":    lighten(darken(adjust-hue(#4f34c9, -109), 2.12), 6.08),
				"brk-secondary-2":    lighten(darken(adjust-hue(#4F00BA, -109), 2.12), 6.08),
				"brk-secondary-3":    lighten(darken(adjust-hue(#7400ba, -109), 2.12), 6.08),
				"brk-secondary-4":    lighten(darken(adjust-hue(#6e50c2, -109), 2.12), 6.08),
				"brk-secondary-5":    lighten(darken(adjust-hue(#6377DF, -109), 2.12), 6.08),
				"brk-secondary-6":    lighten(darken(adjust-hue(#b50aff, -109), 2.12), 6.08),
				"text-base-color-1":  #c4c4c4,
				"text-base-color-2":  darken(adjust-hue(#0F5AE0, -39), 13.73),
				"text-base-color-3":  #f3f3f3,
				"form-color-1":				#e8ecff
), $brk-base-colors);

@function safe-rgb($color) {
	@return unquote("#{red($color)}, #{green($color)}, #{blue($color)}");
}

/* ----------- Palette variable ----------- */
:root {
	@each $color, $value in $brk-base-colors {
		--#{$color}: #{$value};
	}

	// RGB
	@each $color, $value in $brk-base-colors {
		--#{$color}-rgb: #{safe-rgb($value)};
	}

	--b-radius: inherit;
}
/* ----------- Palette variable ----------- */
@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.brs-player-youtube {
	&_adaptive {
		width: 100%;
		position: relative;
		&:after {
			content: '';
			position: relative;
			display: block;
			padding-top: 54.4%;
		}
		iframe {
			position: absolute;
			left: 0;
			top: 0;
			width: 100% !important;
			height: 100% !important;
			z-index: 1;
		}
	}
}

.brs-player-vimeo {
	&_adaptive {
		width: 100%;
		position: relative;
		&:after {
			content: '';
			position: relative;
			display: block;
			padding-top: 54.4%;
		}
		iframe {
			position: absolute;
			left: 0;
			top: 0;
			width: 100% !important;
			height: 100% !important;
			z-index: 1;
		}
	}

}

.brs-player-html5 {
	position: relative;
	&__video-container {
		width: 100%;
		height: auto;
		object-fit: cover;
	}
	&__btn {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, calc(-50% - 20px));
		z-index: 1;
	}
}

.brs-player-facebook {
	&_adaptive {
		height: auto;
		width: 100%;
	}

}

.brk-hosted-video {
	position: relative;
	z-index: 8;
	&::before {
		content: "";
		display: block;
		padding-top: 55.25%;
	}
	@media screen and (max-width: 768px) {
		min-height: 400px;
	}
	box-shadow: 0 6px 20px rgba(0, 0, 0, 0.06);
	&_large-shadow {
		box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
	}
	&__information {
		display: flex;
	}
	&__date {
		text-transform: uppercase;
		margin-right: 14px;
		&-icon {
			margin-right: 8px;
			font-size: 10px;
			position: relative;
			bottom: 2px;
			left: 1px;
		}
	}
	&__comments {
		text-transform: uppercase;
		&-icon {
			margin-right: 8px;
			font-size: 10px;
			position: relative;
			bottom: 2px;
			left: 1px;
		}
  }
  &__date,
  &__comments{
    i{
      vertical-align: middle;
    }
  }
	&__about {
		width: auto;
		max-width: 50%;
		flex-shrink: 0;
		margin-right: 63px;
	}
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
		z-index: -1;
	}
	&__body {
		display: flex;
		flex-direction: column;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
    padding: 26px 40px 35px 41px;
    z-index: 1;
	}
	&__description {
		display: flex;
		@media screen and (max-width: 768px) {
			flex-direction: column;
		}
	}
	&__information {
		display: flex;
	}
	&__date {
		&-icon {
			bottom: 1px;
		}
	}
	&__comments {
		&-icon {
			bottom: 1px;
		}
	}
	&__title {
		margin-top: 5px;
		margin-left: 1px;
		color: $white;
	}
	&__paragraph {
		max-height: 80px;
		overflow: hidden;
		color: $white;
		margin-top: 3px;
	}
	&__btn {
		margin: auto !important;
		cursor: pointer;
		font-size: 16px !important;
	}
	&__video-container {
		display: none;
		width: 75%;
		height: auto;
		padding: 0 !important;
	}

	&_inner {
		video {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			z-index: 99;
		}
  }
  &_short{
    &::before {
      padding-top: 37%;
    }
  }

	&__before {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
	}
}